import { all, takeEvery, call, put } from 'redux-saga/effects';
import { ActionConstants } from './actionConstants';
import {
  CreateNewRoleRequest,
  createCreateNewRoleError,
  createCreateNewRoleSuccess,
} from './actions';
import { createRole } from 'cb-utils/services/roles';
import { push } from 'react-router-redux';
import parseCbError from 'cb-utils/parseCbError';
import { detailUrls } from 'cb-utils/getUrls';

function* callCreateRole(action: CreateNewRoleRequest) {
  try {
    yield call(createRole, action.payload.systemKey, action.payload.role);
    yield put(createCreateNewRoleSuccess());
    yield put(push(detailUrls.roles(action.payload.role.Name)));
  } catch (e) {
    yield put(createCreateNewRoleError(parseCbError(e)));
  }
}

export function* createNewRoleSaga() {
  yield takeEvery(ActionConstants.CREATE_NEW_ROLE_REQUEST, callCreateRole);
}

export default function*() {
  yield all([createNewRoleSaga()]);
}



