/*
 *
 * RolesPage reducer
 *
 */

import assign from 'immutable-assign';
import { ActionTypes } from './actions';
import { ActionConstants } from './actionConstants';

export interface State {
  showNewRoleModal: boolean;
  isCreatingNewRole: boolean;
  error: string;
}

export const initialState: State = {
  showNewRoleModal: false,
  isCreatingNewRole: false,
  error: null,
};

function rolesPageReducer(state: State = initialState, action: ActionTypes) {
  switch (action.type) {
    case ActionConstants.ON_ADD_NEW_ROLE:
      return assign(state, (s) => s.showNewRoleModal, () => true);
    case ActionConstants.DISMISS_NEW_ROLE_MODAL:
      return assign(state, (s) => s.showNewRoleModal, () => false);
    case ActionConstants.CREATE_NEW_ROLE_REQUEST:
      return assign(state, (s) => s.isCreatingNewRole, () => true);
    case ActionConstants.CREATE_NEW_ROLE_SUCCESS:
      return assign(state, (s) => {
        s.isCreatingNewRole = false;
        s.showNewRoleModal = false;
        s.error = null;
        return s;
      });
    case ActionConstants.CREATE_NEW_ROLE_ERROR:
      return assign(state, (s) => {
        s.isCreatingNewRole = false;
        s.error = action.payload.error;
        return s;
      });
    default:
      return state;
  }
}

export default rolesPageReducer;



