/**
 *
 * RoleCard
 *
 */

import React, { SFC } from 'react';
import { RoleModel } from 'cb-utils/console-entity-models';
import { withRouter } from 'react-router';
import { SystemRouteProps } from 'cb-utils/systemRouteProps';
import Desc from 'components/CardDescription';
import NavCard from 'components/NavToDetailCard';

interface RoleCardProps extends SystemRouteProps<{}> {
  role: RoleModel;
}

const RoleCard: SFC<RoleCardProps> = (props) => {
  const roleName = props.role.Name;
  return (
    <NavCard name={roleName}>
      <div>{roleName}</div>
      <div>
        <Desc>{props.role.Description}</Desc>
      </div>
    </NavCard>
  );
};

export default withRouter(RoleCard);



