/*
 *
 * RolesPage
 *
 */

import React from 'react';
import { connect } from 'react-redux';
import Helmet from 'react-helmet';
import { FormattedMessage } from 'react-intl';
import injectSaga from 'utils/injectSaga';
import { compose } from 'redux';
import injectReducer from 'utils/injectReducer';
import { Button } from 'reactstrap';
import {
  selectShowNewRoleModal,
  selectIsCreatingNewRole,
  selectError,
} from './selectors';
import reducer from './reducer';
import { STORE_KEY } from './constants';
import saga from './sagas';
import messages from './messages';
import consoleMessages from 'containers/Console/messages';

import PageHeader from 'components/ConsolePageHeader';
import { rolesActions } from 'containers/Console/ducks/roles';
import ConsolePage from 'components/ConsolePage';
import { BaseRoleModel } from 'cb-utils/console-entity-models';
import { AddIcon, RolesIcon } from 'components/EntityIcons';
import { selectRoles } from '../Console/selectors/roles';
import RoleCard from 'components/RoleCard';
import CardTable from 'components/CardTableForEntities';
import { SystemRouteProps } from 'cb-utils/systemRouteProps';

import NewRoleModal from 'components/NewRoleModal';
import {
  createDismissNewRoleModal,
  createCreateNewRoleRequest,
  createOnAddNewRole,
} from './actions';
import { CBDispatch } from 'cb-utils/typeUtils';
import { createStructuredSelector } from 'reselect';

const defaultSortKey = 'Name';

interface RolesPageProps extends SystemRouteProps<{}> {}

const createColumnsForCardTable = () => {
  return [
    {
      component: (
        <b>
          <FormattedMessage {...messages.nameField} />
        </b>
      ),
      isSortable: true,
      sortKey: defaultSortKey,
    },
  ];
};

export class RolesPage extends React.Component<
  RolesPageProps & DispatchToProps & StateToProps,
  {}
> {
  componentWillMount() {
    this.props.init(this.props.match.params.systemKey);
  }

  render() {
    const {
      roles,
      showNewRoleModal,
      dismissNewRoleModal,
      createNewRole,
      isCreatingNewRole,
      error,
    } = this.props;
    return (
      <ConsolePage subheader={this.renderSubHeader()}>
        <Helmet
          title="ClearBlade - Roles"
          meta={[{ name: 'description', content: 'Description of RolesPage' }]}
        />
        {showNewRoleModal && (
          <NewRoleModal
            toggle={dismissNewRoleModal}
            create={(role: BaseRoleModel) => createNewRole(role)}
            isCreating={isCreatingNewRole}
            error={error}
          />
        )}
        <CardTable
          defaultSortKey={defaultSortKey}
          headerColumns={createColumnsForCardTable()}
          entities={roles}
          entityCard={RoleCard}
          cardProp="role"
        />
      </ConsolePage>
    );
  }

  renderSubHeader = () => {
    return (
      <React.Fragment>
        <PageHeader
          header={
            <div>
              <RolesIcon />{' '}
              <FormattedMessage {...consoleMessages.assetClassRoles} />
            </div>
          }
          actionButton={() => (
            <Button
              onClick={this.props.onAddNewRole}
              color="primary"
              data-cy="add-role"
            >
              <AddIcon />
              &nbsp;
              <FormattedMessage {...messages.addRole} />
            </Button>
          )}
        />
      </React.Fragment>
    );
  };
}

type StateToProps = ReturnType<typeof mapStateToProps>;
const mapStateToProps = createStructuredSelector({
  roles: selectRoles(),
  showNewRoleModal: selectShowNewRoleModal(),
  isCreatingNewRole: selectIsCreatingNewRole(),
  error: selectError(),
});

type DispatchToProps = ReturnType<typeof mapDispatchToProps>;
function mapDispatchToProps(dispatch: CBDispatch, ownProps: RolesPageProps) {
  return {
    init: (key: string) => dispatch(rolesActions.fetchAll.request(key)),
    dismissNewRoleModal: () => dispatch(createDismissNewRoleModal()),
    onAddNewRole: () => dispatch(createOnAddNewRole()),
    createNewRole: (role: BaseRoleModel) =>
      dispatch(
        createCreateNewRoleRequest(ownProps.match.params.systemKey, role),
      ),
  };
}

const withConnect = connect(mapStateToProps, mapDispatchToProps);

const withReducer = injectReducer({ key: STORE_KEY, reducer });
const withSaga = injectSaga({ key: STORE_KEY, saga });
export default compose(withReducer, withSaga, withConnect)(RolesPage);



