import { createSelector } from 'reselect';
import { STORE_KEY } from './constants';
import { State } from './reducer';
import { AppState } from 'reducers';

/**
 * Direct selector to the rolesPage state domain
 */
const selectRolesPageDomain = () => (state: AppState): State =>
  state[STORE_KEY];

/**
 * Other specific selectors
 */

/**
 * Default selector used by RolesPage
 */

const makeSelectRolesPage = () =>
  createSelector(selectRolesPageDomain(), (substate) => substate);

const selectShowNewRoleModal = () =>
  createSelector(
    makeSelectRolesPage(),
    (substate) => substate.showNewRoleModal,
  );

const selectIsCreatingNewRole = () =>
  createSelector(
    makeSelectRolesPage(),
    (substate) => substate.isCreatingNewRole,
  );

const selectError = () =>
  createSelector(makeSelectRolesPage(), (substate) => substate.error);

export default makeSelectRolesPage;
export {
  selectRolesPageDomain,
  selectShowNewRoleModal,
  selectIsCreatingNewRole,
  selectError,
};



