/**
 *
 * NewRoleModal
 *
 */

import React from 'react';
// import styled from 'styled-components';

import { Form, Input, Label, FormGroup, Button } from 'reactstrap';

import { FormattedMessage } from 'react-intl';
import messages from './messages';

import InfoModal from 'components/InfoModal';
import { BaseRoleModel } from 'cb-utils/console-entity-models';
import consoleMessages from 'containers/Console/messages';

import LoadingIndicator from 'components/LoadingIndicator';

interface NewRoleModalProps {
  toggle: () => void;
  create: (role: BaseRoleModel) => void;
  isCreating: boolean;
  error: string;
}

interface NewRoleModalState {
  role: BaseRoleModel;
}

class NewRoleModal extends React.Component<
  NewRoleModalProps,
  NewRoleModalState
> {
  // eslint-disable-line react/prefer-stateless-function

  state: NewRoleModalState = {
    role: {
      name: '',
      Name: '',
      Description: '',
    },
  };

  render() {
    return (
      <InfoModal
        toggleOpen={this.props.toggle}
        isOpen
        title={<FormattedMessage {...messages.modalTitle} />}
        body={this.renderBody()}
        footer={() => this.renderFooter()}
      />
    );
  }

  renderBody() {
    const { Name, Description } = this.state.role;
    return (
      <Form
        onSubmit={(e) => {
          e.preventDefault();
          this.props.create(this.state.role);
        }}
      >
        <FormGroup>
          <Label>
            <FormattedMessage {...consoleMessages.name} />
          </Label>
          <Input
            value={Name}
            name="role-name"
            onChange={(e) => this.handleRolePropChange('Name', e.target.value)}
            type="text"
            autoFocus
            placeholder="RoleName"
          />
        </FormGroup>
        <FormGroup>
          <Label>
            <FormattedMessage {...consoleMessages.description} />
          </Label>
          <Input
            value={Description}
            name="role-description"
            onChange={(e) =>
              this.handleRolePropChange('Description', e.target.value)
            }
            type="textarea"
          />
        </FormGroup>
        <div className="invalid-feedback" style={{ display: 'block' }}>
          {this.props.error}
        </div>
      </Form>
    );
  }

  renderFooter() {
    const { create, isCreating } = this.props;
    const { role } = this.state;
    return (
      <Button
        type="submit"
        disabled={role.Name === ''}
        color="primary"
        onClick={() => create(role)}
      >
        {isCreating ? (
          <LoadingIndicator />
        ) : (
          <FormattedMessage {...messages.createRole} />
        )}
      </Button>
    );
  }

  handleRolePropChange(field: keyof BaseRoleModel, value: string) {
    this.setState({
      ...this.state,
      role: {
        ...this.state.role,
        [field]: value,
      },
    });
  }
}

export default NewRoleModal;



